<template>
  <div>
    <div class="row justify-content-center">
      <div class="col col-lg-6 col-md-6 col-sm-12 col-12">
        <vx-card>
          <div class="flex justify-between" style="direction: ltr">
            <div class="ml-2">{{writingQuestion}}</div>
            <div style="border-left: 1px solid #c6c6c6">
              <h3 class="mr-3 ">{{timeText}}</h3>
            </div>
          </div>
        </vx-card>
      </div>
    </div>
    <div class="row justify-content-center mt-8">
      <div class="col col-lg-6 col-md-6 col-sm-12 col-12">
        <vx-card class="ltr">
          <vs-textarea v-model="writingContent" height="500px"  style="direction: ltr"/>
          <vs-button id="finish-writing-btn" @click="finishAndSave">Finish and Save writing</vs-button>
        </vx-card>
      </div>
    </div>

    <vs-popup title="writing timeout" :active.sync="alertActive">
      <div class="row justify-content-center">
        <p>
          your writing time has finished
        </p>
      </div>
      <vs-divider></vs-divider>

      <div class="vx-row justify-end">
        <div class="vx-col flex">
          <vs-button @click="alertActive = false">Ok, Got it</vs-button>
        </div>
      </div>
    </vs-popup>

  </div>
</template>

<script>
import mixins from '../../../mixins';
import axios from "../../../http/axios";

export default {
  mixins: [mixins],
  name: 'WritingTest',
  data(){
    return {
      writingQuestion: this.$route.params.writingQuestion,
      writingContent: null,
      time: 2400000, // 40 minutes
      // time: 4000, // 40 minutes
      timeText: "40:00",
      timer: null,

      alertActive: false
    }
  },
  mounted () {
    if (this.writingQuestion == undefined) {
      this.$router.push({
        name: 'writing-index',
      });
    }

    this.startCountDownTimer()

  },

  methods: {
    startCountDownTimer(){
      this.timer = setInterval(() => {


        var minutes = Math.floor((this.time % (1000 * 60 * 60)) / (1000 * 60));
        var seconds = Math.floor((this.time % (1000 * 60)) / 1000);

        minutes = minutes < 10 ? "0" + minutes : minutes;
        seconds = seconds < 10 ? "0" + seconds : seconds;

        this.timeText = `${minutes}:${seconds}`;
        this.time -= 1000;

        if (this.time < 0) {
          this.onWritingTimeFinished()
        }
      }, 1000);
    },

    onWritingTimeFinished(){
      clearInterval(this.timer);
      this.alertActive = true;
    },

    finishAndSave(){
      this.startLoading('finish-writing-btn');
      axios.post(`/api/english/writings/`,{
        question: this.writingQuestion,
        answer: this.writingContent
      },{
        headers:{
          Accept: "application/json",
          Authorization: this.getAccessToken()
        }
      }).then(res => {
        this.stopLoading('finish-writing-btn');
        if (res.status === 200){
          this.simpleNotify(this.NOTIFY_SUCCESS_TITLE,
          'saved successfully, to view results go to library',
           'success');
           setTimeout(()=>{
             this.$router.push({name: 'writing-index'});
           }, 1000);
        }else {
          this.simpleNotify(this.NOTIFY_DANGER_TITLE, 'something went wrong', 'danger');
        }
      });
    }
  },

  destroyed(){
    clearInterval(this.timer)
  }
}
</script>

<style>
.vs-textarea{
  height: 500px!important;
}
</style>
